function Profile({ userAvatar }) {
  return (
    <>
      <div>
        <img src={userAvatar} alt="user avatar" width="50px" />
        {/* <p>{user.name}</p> */}
        {/* <img src={user.avatar} alt="" width="48" /> */}
      </div>
    </>
  );
}

Profile.defaultProps = {
  avatar:
    "data:image/png;base64,",
};

export default Profile;
